import { key } from './meta';
import type { ConvertToPluginCommands, ConvertToPluginQueries, ConvertToPluginActionCreators, ConvertToPluginActionPredicates, Controller } from '@ali/4ever-cangjie';
import type { commands } from '../commands';
import type { Queries } from '../queries';
import type { actions, isActions } from '../actions';

/** 插件 config type */
export type PluginConfig = {
  /**
   * 是否开启 snippet 功能
   */
  enabled?: boolean;
  /**
   * LazyLoad async loader
   */
  lazyload?: any;
};

declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginCommands extends ConvertToPluginCommands<typeof commands>{}
  interface PluginQueries extends ConvertToPluginQueries<Queries>{}
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions>{}
  interface PluginActionPredicates extends ConvertToPluginActionPredicates<typeof isActions>{}

  /** 插件配置 */
  interface BiPluginConfig {
    [key]?: PluginConfig;
  }

  interface MobileBiPluginConfig {
    [key]?: PluginConfig;
  }
}
